<template>
  <ul class="formarea">
    <li>
      <label class="lit"></label>
      <input type="text" placeholder="" class="textbox" v-model="username">
    </li>
    <li>
      <label class="lit"></label>
      <input type="password" placeholder="" class="textbox" v-model="password">
    </li>
    <li class="liLink lg_liLink">
      <span>
        <label>
          <input type="checkbox">
        </label>
      </span>
      <span>
        <router-link to="reg"></router-link>
      </span>
      <span>
        <a href="find_pwd.html">?</a>
      </span>
    </li>
    <li>
      <input type="button" value="" @click="checkLogin">
    </li>
  </ul>
</template>

<script>
import * as types from '../store/types';

export default {
  data() {
    return {
      username:'',
      password:''
    }
  },
  methods: {
    checkLogin(){
      this.$store.dispatch({
        type:types.CHECK_USER,
        username:this.username,
        password:this.password
      }).then(
        (auth)=>{
          //state  this.$store.state.user.auth
          // res
          if(this.$store.state.user.auth){
            this.$router.push('/user')
          }else{
            // this.$router.push('/reg')
            alert("!")
          }
        }
      )
    }
  },

}
</script>